@media screen and (max-width: 959px) {
  .header {
    height: 68px;
    background-color: #fff;
    transition: all .6s;
  }
  .header__inner {
    max-width: 100%;
  }
  .header__logo {
    display: block;
    width: 200px;
    margin-left: 16px;
  }
  .header__logo img {
    width: 100%;
  }
  .header__nav {
    display: block;
    position: fixed;
    top: 0;
    right: -320px;
    /* これで隠れる */
    z-index: 200;
    width: 300px;
    /* スマホに収まるくらい */
    height: 100vh;
    overflow-y: auto;
    /* メニューが多くなったらスクロールできるように */
    background-color: #fff;
  }
  .header__globalNav__lists {
    display: block;
    list-style: none;
    margin: 0;
    padding: 0;
