  .header__globalNav__item a {
    display: block;
    padding: 8px 0;
    border-bottom: 1px solid #eee;
    color: #111;
    text-decoration: none;
  }
  .hamburger {
    display: block;
    position: fixed;
    top: 13px;
    right: 13px;
    z-index: 300;
    width: 40px;
    /* クリックしやすいようにちゃんと幅を指定する */
    height: 40px;
    /* クリックしやすいようにちゃんと高さを指定する */
    cursor: pointer;
  }
  .hamburger__line {
    position: absolute;
    left: 7px;
    width: 26px;
    height: 4px;
    border-radius: 2px;
    background-color: #3e3e3e;
    transition: all .6s;
  }
  .hamburger__line--1 {
    top: 12px;
  }
  .hamburger__line--2 {
    top: 20px;
  }
  .hamburger__line--3 {
    top: 28px;
  }
  .black-bg {
    visibility: hidden;
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100vw;
    height: 100vh;
    background-color: #000;
    cursor: pointer;
  }
  /* 表示された時用のCSS */
  .nav-open .header__nav {
    display: flex;
    align-items: center;
    right: 0;
    text-align: center;
    transition: all .6s;
  }
  .nav-open .black-bg {
    visibility: visible;
    opacity: .8;
    transition: all .6s;
  }
  .nav-open .hamburger {
    transform: translateX(-300px);
    transition: all .6s;
  }
  .nav-open .hamburger__line {
    background-color: #ffffff;
  }
  .nav-open .hamburger__line--1 {
    top: 20px;
    transform: rotate(45deg);
  }
  .nav-open .hamburger__line--2 {
    left: 50%;
    width: 0;
  }
  .nav-open .hamburger__line--3 {
    top: 20px;
    transform: rotate(-45deg);
  }
}

